@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&family=Ubuntu:wght@400;700&display=swap");

.nav {
   height: calc(100vh - 50px);
}

.box {
   box-shadow: 0 5px 10px rgba(30, 32, 34, 0.3);
}

@layer base {
   html {
      @apply scroll-smooth;
   }

   button {
      @apply px-6 py-2 bg-c4 text-c1 tracking-wider font-bold border-2 border-c4 
      rounded hover:bg-transparent hover:text-c4 transition duration-150;
   }

   h1 {
      @apply text-center font-ubuntu text-4xl font-bold relative z-20;
   }

   hr {
      @apply h-[3.5px] bg-c4 w-40 mx-auto mt-2 mb-12;
   }

   hr::after {
      @apply content-['o'] text-xl font-semibold absolute left-[50%] translate-x-[-50%] -mt-[15px] px-2;
   }

   pre {
      @apply font-[Arial] m-0;
   }
}
